<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
        <ctitle :title="'一级内容分类信访量'"></ctitle>
        <div class="left-t">
          <div class="count">
            <allCount class="all-count" :count="allcount"></allCount>
          </div>
          <div class="detail">
            <detailList :params="detailList"></detailList>
          </div>
        </div>
        <div class="inner3">
          <div class="chart">
            <pie-chart3D :params="wtflPieParams" @change='wtflChange'></pie-chart3D>
          </div>
        </div>
      </div>
      <div class="w-right">
        <ctitle :title="yjnr+'信访量分布'"></ctitle>
				<div class="c-bg"></div>
				<Map :params="mapParams" class="map" ></Map>
      </div>
    </div>
  </div>
</template>

<script>
  import { get_http } from "@/api/index";
  import { gradientColor } from "@/utils/gradient";
  import ctitle from "@/components/title/index";
  import PieChart3D from "@/components/pie/PieChart3D";
  import allCount from "@/components/list/allCount";
  import detailList from "@/components/list/detailList";
	import Map from "@/components/map/Mapyn";
  export default {
    name: "dyfb",
    components: {
      ctitle,
      PieChart3D,
      allCount,
			detailList,
			Map
      // hotList,
      // popLists
    },
    data() {
      return {
        //一级内容分布
        wtflPieParams: {
          data: [],
          color: ["#6f69ff", "#59eaff", "#74d650", "#f0ff87", "#ff965d", "#5fffb6", "#61c6ff", "#dda2ea", "#745cc5", "#ffc343", "#fb548f", "#a7f68d", "#6994ff"],
          angle: 50,
          radius: "40%",
          labelRadius: 60,
          depth3D: 35,
          pullOutRadius: 30,
          styleObj: {
            width: "100%",
            height: "100%"
          },
          defaultStatus: true
        },
        yjnr: '城乡建设',
        detailList: {},
        // 初信重信联名信占比
        allcount: -1,
				hotListIndex: 0,
				mapParams: {
          canvasStyle: {
            width: "100%",
            height: "98%",
          },
          data: [],
          options: {
            isHome: false,
            wtflname: "城乡建设",
            symbolSize: 26,
            scatterColor: '#ff2ab0',
            emphasisColor: '#ffa400',
            JSON: "530000",
            xName: "name",
						yName: "value",
						top: 0,
            map: {
              "value": "信访量"
            },
            carousel: false
          }
				},
      };
    },
    mounted() {
      this.getAPI(this.date)
    },
    methods: {
      wtflChange(obj) {
        this.mapParams.options.wtflname = obj.name;
        this.yjnr = obj.name;
        this.getMapAPI(this.date)
      },

      getAPI(date) {
        
        // 1.一级内容分类信访量（总数）
        get_http("xftstjfxNrflDyfbXflTotal", {
          day: `[${date.startDate},${date.endDate}]`,
        })
        .then(res => {
          this.allcount = res.data.data[0].value;
        })
        .catch(err => {
          console.log(err);
        });
        // 2.一级内容分类信访量(列表)
        get_http("xftstjfxNrflDyfbXfllb", {
          day: `[${date.startDate},${date.endDate}]`,
          limit: 3
        })
        .then(res => {
          this.detailList = res.data;
        })
        .catch(err => {
          console.log(err);
        });
        // 3.一级内容分类信访量3d饼图
        get_http("xftstjfxNrflDyfbXflPie", {
          day: `[${date.startDate},${date.endDate}]`,
        })
        .then(res => {
          this.wtflPieParams.data = res.data.data;
          this.mapParams.options.wtflname = this.wtflPieParams.data[0].name;
          this.yjnr = this.wtflPieParams.data[0].name;
        })
        .catch(err => {
          console.log(err);
        });
        this.getMapAPI(date)
      },  
      getMapAPI(date) {
        // 4.全省信访量
        get_http("xftstjfxNrflDyfbMap", {
          day: `[${date.startDate},${date.endDate}]`,
          yjnr: this.yjnr
        })
          .then(res => {
            this.mapParams.options.map = res.data.map
            this.mapParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
					});
      }
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val, oval) {
        if (oval.startDate || oval.endDate) {
          this.getAPI(val);
        }
      },
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .content
      width 100%
      height 100%
      display flex
      padding 0.2rem .3rem
      box-sizing border-box
      .w-left
        width 9.5rem
        height 100%
        position relative
        font-size 0
        display flex
        flex-direction column
        .left-t
          width 100%
          margin-top 0.1rem
          flex 2
          display flex
          justify-content space-around
          align-items center
          .detail,.count
            flex 1
        .inner3
          flex 3
          .chart
            width 100%
            height 100%
      .w-right
        position relative
        flex 1
        height 100%
        display flex
        flex-direction column
        .map-chart
          background none
          position absolute
          top -45px
				.c-bg
					position absolute
					width 100%
					height 100%
					z-index 0
					top 0
					left 0
					background url("../../../assets/map-bg.png") no-repeat center bottom
</style>
