<!--
  @auth:lcw
  云南地图+散点图
-->
<template>
    <div
      class="map-chart"
      v-loading="!loadFlag"
      :element-loading-background="$STANDARD.loadingBgColor"
      ref="chartEl"
      :style="{width : params.canvasStyle.width, height: params.canvasStyle.height}"
    >
    </div>
</template>

<script>
  import * as echarts from 'echarts'
  import {getmap_http} from "@/api/index";

  export default {
    name: 'yunnan',
    props: {
      params: Object,
      default: function() {
        return {
          options: {
            emphasis: {}
          }
        }
      },
      wtflname: String
    },
    data() {
      return {
        myChart: null,
        rollFlag: true,
        loadFlag: false
      };
    },
    mounted() {
      this.init();
      this.$once('hook:beforeDestroy', () => {
        if (this.interval) {
          clearInterval(this.interval);
        }
        this.myChart && this.myChart.dispose();
      })
    },
    methods: {
      init() {
        if (this.myChart) {
          this.myChart.dispose();
          this.myChart = null;
        }

        if (this.params.data.length === 0) return;
        let options = this.params.options;
        
        let geoCoordMap = {
          "曲靖市": [103.897851, 25.601557],
          "昆明市": [102.812251, 25.340609],
          "玉溪市": [102.003907, 24.050461],
          "保山市": [99.067133, 25.011802],
          "昭通市": [103.917216, 27.736999],
          "丽江市": [100.633026, 26.972108],
          "普洱市": [100.672344, 23.077321],
          "临沧市": [99.68697, 23.986567],
          "楚雄州": [101.546046,25.541988],
          "红河州": [103.084182, 23.366775],
          "文山州": [104.74401, 23.76951],
          "西双版纳": [100.897941, 22.201724],
          "大理州": [100.025668, 25.789449],
          "德宏州": [98.078363,24.536694],
          "怒江州": [99.194304, 26.650949],
          "迪庆州": [99.506463, 27.986853],
          
        };

        function convertData(data) {
          var res = [];
          for (let i = 0; i < data.length; i++) {
            var geoCoord = geoCoordMap[data[i][options.xName]];
            if (geoCoord) {
              res.push({
                name: data[i].name,
                value: geoCoord.concat(data[i][options.yName]),
                visualMap: false
              });
            }
          }
          return res;
        }
        getmap_http(`${this.params.options.JSON}.json`).then(json => {
          this.loadFlag = true;
          echarts.registerMap("yunnan", json.data);
          this.myChart = echarts.init(this.$refs.chartEl);
          let maxValue = 0, data = [];
          let dataKeys = Object.keys(options.map);
          let datavalues = Object.values(options.map);
          if (options.mockData) {
            for (let item of json.data.features.values()) {
              function random(lower, upper) {
                return Math.floor(Math.random() * (upper - lower + 1)) + lower;
              }

              let obj = {
                name: item.properties.name,
                value: random(1, 100),
              };
              data.push(obj);
              for (let item of data.values()) {
                maxValue = (maxValue > item.value ? maxValue : item.value)
              }
            }
          } else {
            for (let item of this.params.data.values()) {
              maxValue = (maxValue > item[options.yName] ? maxValue : item[options.yName]);
              let obj = {
                name: item[options.xName],
                value: item[options.yName],
                zlevel: 1
              };

              dataKeys.forEach((it, ind) => {
                obj[it] = item[it]
              });
              data.push(obj);
            }
          }

          this.option = {
            tooltip: {
              backgroundColor: "rgba(9, 39, 109, 0.8)",
              padding: [10, 20],
              formatter: params => {
                if (options.isHome) {
                  return `
                  <div class="tooltips">
                    <p><span class="number">${params.data[dataKeys[0]]}</span>件次</p>
                    <p><span class="name">${datavalues[1]}</span><span class="number">${params.data[dataKeys[1]]}</span></p>
                    <p><span class="name">${datavalues[2]}</span><span class="number">${params.data[dataKeys[2]]}</span></p>
                    <p><span class="name">${datavalues[3]}</span><span class="number">${params.data[dataKeys[3]]}</span></p>
                  </div>`;
                } else if (options.wtflname){
                  return `
                  <div class="tooltips">
                    <p>${options.wtflname}</p>
                    <p><span class="col">${params.data[dataKeys[0]]}</span>${datavalues[0]}</p>
                    <p><span class="col">${params.data[dataKeys[1]]}</span>${datavalues[1]}</p>
                  </div>`;
                } else if(options.tooltipNotShow) {
                  return 
                } else {
                  return params.name + ' : ' + params.value
                } 
              }
            },
            geo: [
              {
                show: true,
                map: 'yunnan',
                aspectScale: 0.9,
                zoom: 1,
                label: {
                  normal: {
                    show: false,
                    formatter: (params) => {
                      if (params.value) {
                        return params.name;
                      }
                    },
                    position: 'bottom',
                    textStyle: {
                      color: "#fff",
                      fontSize: 16
                    }
                  },
                  emphasis: {
                    show: false,
                    textStyle: {
                      color: "#fff"
                    }
                  }
                },
                roam: false,
                itemStyle: {
                  normal: {
                    borderColor: '#fff',
                    // borderWidth: 2,
                    areaColor: '#fff',
                    shadowColor: '#0c3367',
                    shadowOffsetX: 10,
                    shadowOffsetY: 5,
                  },
                  emphasis: {
                    // areaColor: '#fefa82',
                    label: {
                      show: false
                    },
                    //  ...options.emphasis,
                     borderWidth: 0,
                    borderColor: options.emphasisBorderColor? options.emphasisBorderColor : '#fff',
                    areaColor: options.emphasisAreaColor ? options.emphasisAreaColor : '#0969bf'
                  }
                },
                z: 2
              },
            ],
            visualMap: {
              show: false,
              min: 0,
              max: maxValue,
              text: ["高", "低"], // 文本，默认为数值文本
              calculable: true,
              itemWidth: "12%",
              itemHeight: "110%",
              textStyle: {
                color: "#fff",
                fontSize: "14%"
              },
              bottom: "10%",
              left: "5%",
              inRange: {
                color: [
                  // "#fff",
                  // "#ffad83",
                  // "#f2684f"
                  // "rgba(19,240,255,1)",
                  // "rgba(4,131,222,1)",
                  // "rgba(8,53,109,1)"
                ]
              }
            },
            series: [
              {
                type: 'map',
                map: 'yunnan',
                aspectScale: 0.9,
                zoom: 1,
                label: {
                  normal: {
                    show: false
                  },
                  emphasis: {
                    show: false
                  },
                },
                itemStyle: {
                  normal: {
                    borderColor: options.borderColor? options.borderColor : '#fff',
                    borderWidth: 1,
                    areaColor: options.normalAreaColor ? options.normalAreaColor : '#0969bf'
                  },
                  emphasis: {
                    ...options.emphasis,
                    borderColor: options.emphasisBorderColor? options.emphasisBorderColor : '#fff',
                    areaColor: options.emphasisAreaColor ? options.emphasisAreaColor : '#0969bf'
                  }
                },
                data: data
              },
              // 散点，暂时去掉
              {
                name: 'scatter',
                // type: 'scatter',
                type: 'effectScatter',
                tooltip: {
                  show: false,// 此处为false并不能直接去掉散点图tooltip，需使用formatter达到隐藏tooltip效果，原因不明，待解决
                },
                coordinateSystem: 'geo',
                symbolSize: (val) => {
                  return options.symbolSize ? options.symbolSize : 10;
                },
                label: {
                  normal: {
                    show: true,
                    formatter: (params) => {
                      if (params.value) {
                        return params.name;
                      }
                    },
                    position: 'bottom',
                    textStyle: {
                      color: "#fff",
                      fontSize: 16
                    }
                  },
                  emphasis: {
                    areaColor: options.emphasisAreaColor ? options.emphasisAreaColor : '#0969bf',
                    textStyle: {
                      color: "#fff",
                      fontWeight: "bolder"
                    }
                  },
                },
                rippleEffect: {
                  brushType: 'stroke'
                },
                hoverAnimation: true,
                itemStyle: {
                  normal: {
                    color: options.scatterColor,
                    // borderColor: 'transparent',
                    // borderWidth: 10,
                    // shadowColor: '#3cfff8',
                    // shadowBlur: 10
                  },
                  emphasis: {
                    show: true,
                    color: options.emphasisColor,
                  }
                },
                data: convertData(this.params.data)
              }
            ]
          };
          this.myChart.setOption(this.option);
          if (options.carousel) {
            this.chartMove();
          }
          // 鼠标点击
          // this.myChart.on("click", params => {
          //   this.$emit("change", params.name);
          // });
          let clickCurIndex = -1;
          this.myChart.on("click", params => {
            let name = params.name;
            // 未设置轮播时添加点击事件
            if ((this.params.options.JSON==='530000')) {
              if (clickCurIndex !== params.dataIndex) {
                // 取消之前高亮的图形
                this.myChart.dispatchAction({
                  type: "downplay",
                  dataIndex: clickCurIndex
                });
                // 高亮当前图形
                this.myChart.dispatchAction({
                  type: "highlight",
                  dataIndex: params.dataIndex
                });
                clickCurIndex = params.dataIndex;
                name = params.name;
              } else {
                // 重复点击
                return
                // 取消之前高亮的图形
                // this.myChart.dispatchAction({
                //   type: "downplay",
                //   dataIndex: clickCurIndex
                // });
                // name = '全省';
              }
            }

            this.$emit("change", name);
          });

          // 鼠标移入
          this.myChart.on("mouseover", params => {
            if (options.carousel) {
              this.rollFlag = false;
              if (params.dataIndex === this.app.currentIndex) {
                // 序号相同不取消高亮
                return;
              } else {
                // 取消之前高亮的图形
                this.myChart.dispatchAction({
                  type: "downplay",
                  seriesIndex: 0,
                  dataIndex: this.app.currentIndex
                });
              }
            }
          });
          // 鼠标移除
          this.myChart.on("mouseout", params => {
            this.rollFlag = true;
          });
        });
      },
// 地图tooltip自动移动
      chartMove() {
        this.app = {};
        this.app.currentIndex = -1;

        if (this.interval) {
          clearInterval(this.interval);
        }
        this.interval = setInterval(() => {
          let dataLen = this.option.series[0].data.length;

          if (this.rollFlag) {
            // 取消之前高亮的图形
            this.myChart.dispatchAction({
              type: "downplay",
              seriesIndex: 0,
              dataIndex: this.app.currentIndex
            });

            this.app.currentIndex = (this.app.currentIndex + 1) % dataLen;
            // 高亮当前图形
            this.myChart.dispatchAction({
              type: "highlight",
              seriesIndex: 0,
              dataIndex: this.app.currentIndex
            });
            this.myChart.dispatchAction({
              type: "showTip",
              seriesIndex: 0,
              dataIndex: this.app.currentIndex
            });
          }
        }, 2000);
      },
    },
    computed: {
      styleType() {
        return this.$store.state.styleType;
      },
      realData() {
        return this.params.data
      },
      // JSON() {
      //   return this.params.options.JSON
      // }
    },
    watch: {
      realData: {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      },
      // "params.options.wtflname": {
      //   handler(newVal, oldVal) {
      //     this.init();
      //   },
      //   deep: true
      // }
      // JSON: {
      //   handler(newVal, oldVal) {
      //     this.init();
      //   },
      //   deep: true
      // }
    }
  };
</script>

<style lang="stylus">
  .map-chart
    background url("./img/bg.png") no-repeat
  .tooltips
    p
      color rgba(19, 240, 255, 0.6)
      font-size 0.16rem

      .name
        margin-right 0.1rem
        color #fff

      .number, .col
        text-shadow 0 0 6px #276bf4, 0 0 10px #276bf4, 0 0 20px #276bf4
        font-size 0.2rem
        color #fff
        margin-right 0.05rem

    p + p
      margin-top 0.1rem
      .number
        text-shadow none
        color #5aceff
</style>
